document.addEventListener('DOMContentLoaded', function() {
    const imageInput = document.getElementById('image-input');
    const originalImage = document.getElementById('original-image');
    const croppedImage = document.getElementById('cropped-image');
    const cropBtn = document.getElementById('crop-btn');
    const downloadBtn = document.getElementById('download-btn');
    const resetBtn = document.getElementById('reset-btn');
    const editorSection = document.querySelector('.editor-section');
    const resultSection = document.querySelector('.result-section');
    
    let cropper;
    
    // –ó–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
    imageInput.addEventListener('change', function(e) {
        if (e.target.files.length) {
            const file = e.target.files[0];
            
            // –ü—Ä–æ–≤–µ—Ä–∫–∞ —Ä–∞–∑–º–µ—Ä–∞ —Ñ–∞–π–ª–∞
            if (file.size > 300 * 1024) {
                alert('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤—ã–±–µ—Ä–∏—Ç–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ä–∞–∑–º–µ—Ä–æ–º –¥–æ 300 –ö–ë');
                return;
            }
            
            const reader = new FileReader();
            reader.onload = function(event) {
                originalImage.src = event.target.result;
                
                // –ü–æ–∫–∞–∑–∞—Ç—å —Ä–µ–¥–∞–∫—Ç–æ—Ä
                editorSection.classList.remove('hidden');
                resultSection.classList.add('hidden');
                downloadBtn.disabled = true;

                if (cropper) {
                    cropper.destroy();
                }
                
                cropper = new Cropper(originalImage, {
                    aspectRatio: NaN,
                    viewMode: 1,
                    autoCropArea: 0.8,
                    responsive: true,
                    guides: true
                });
            };
            reader.readAsDataURL(file);
        }
    });
    
    // –û–±—Ä–µ–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
    cropBtn.addEventListener('click', function() {
        if (!cropper) return;
        
        // –û–±—Ä–µ–∑–∞–Ω–Ω–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –≤ —Ñ–æ—Ä–º–∞—Ç–µ base64
        const canvas = cropper.getCroppedCanvas({
            minWidth: 64,
            minHeight: 64,
            maxWidth: 4096,
            maxHeight: 4096,
            fillColor: '#fff',
            imageSmoothingEnabled: true,
            imageSmoothingQuality: 'high',
        });
        
        if (canvas) {
            croppedImage.src = canvas.toDataURL('image/jpeg');
            resultSection.classList.remove('hidden');
            downloadBtn.disabled = false;
        }
    });
    
    // –°–∫–∞—á–∏–≤–∞–Ω–∏–µ –æ–±—Ä–µ–∑–∞–Ω–Ω–æ–≥–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
    downloadBtn.addEventListener('click', function() {
        if (!croppedImage.src || croppedImage.src === '#') return;
        
        const link = document.createElement('a');
        link.download = 'cropped-image.jpg';
        link.href = croppedImage.src;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    });
    
    // –°–±—Ä–æ—Å
    resetBtn.addEventListener('click', function() {
        if (cropper) {
            cropper.reset();
            resultSection.classList.add('hidden');
            downloadBtn.disabled = true;
        }
    });
});